$('.inform_nav').click(function () {
  $('.inform_nav').toggleClass('active')
})
$('.inform_nav_item').click(function (event) {
  if(!$(event.currentTarget).parent(".inform_nav").hasClass("active")){
     event.preventDefault();
  }
})